html {
  background-color: #d9e3e2;
  width: 100%;
}
.App {
  text-align: center;
  color: rgb(196, 190, 190);
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100dvh;
  width: 100%;
}

.project-list {
  height: 100%;
  width: 100%;
  display: flex;
  overflow: hidden;
  border-radius: 24px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.Title {
  padding-left: 20px;
}
.App-header {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: #a1bfd3;
  background-color: #2d2d30;
  width: 100%;
  height: 8dvh;
}

.project-carousel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  max-width: 500px;
  width: 100vw;
}

.project-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 306px;
}

.chooseIndex {
  display: flex;
  color: #2d2d30;
}
button {
  border: none;
  background-color: rgb(0, 0, 0, 0);
  font-size: 30px;
  color: #2d2d30;
  cursor: pointer;
}

.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 300px;
  min-width: 300px;
  background-color: #2d2d30;
  border-radius: 24px;
  border: 3px solid #a1bfd3;
  transition: translate 200ms ease-in-out;
}

.description {
  text-align: left;
  height: 170px;
  margin: 0;
  font-size: 15px;
  width: 85%;
}

.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 20%;
}

a {
  padding: 4px;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.5rem;
  width: 3.5rem;
  border-radius: 8px;
  text-decoration: none;
  background-color: #1d4865;
  color: rgb(203, 197, 197);
  border: 1.5px solid #d9e3e2;
}

.Panda {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  color: black;
  height: 27dvh;
  width: 27dvh;
  font-size: 1dvh;
  font-weight: bold;
  background-color: white;
  box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.2), 0 0px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 50%;
}

h1 {
  margin: 0;
  margin-top: 10px;
  font-size: 22px;
  color: rgb(201, 192, 124);
}

.footer {
  height: 8dvh;
  background-color: #2d2d30;
  width: 100%;
}

